

.popup { 
    .popWrap { height: 80%; 
        .pop_cont { padding: 0; margin: 0; background-color: #ccc; display: flex; flex-direction: column; border-radius: 16px;
            .pdf_content { flex: 1; padding: 24px 40px; overflow: auto;}
        }
    }

}
.pdf_header { display: flex; justify-content: space-between; align-items: center; padding: 16px 40px; border-bottom: 1px solid #EEEEEE; box-shadow: 0 2px 8px 4px rgba(0, 0, 0, 0.1); background-color: #FFF; color: #ddd; 
    > * { display: flex; column-gap: 20px; align-items: center; width: 33.33%;}
}
.page { justify-content: center;
    > div { display: flex; align-items: center; column-gap: 10px;
        span, strong { padding: 0 5px;}
    }
    .btn_prev, .btn_next { --background: transparent; background-position: center; width: 28px; height: 28px; background: #E8E8E8 center no-repeat; text-indent: -99999px;  border-radius: 4px; -webkit-border-radius: 4px; -moz-border-radius: 4px; -ms-border-radius: 4px; -o-border-radius: 4px; 
        &:hover { background-color: rgba(68, 67, 63, 0.1); --background: rgba(68, 67, 63, 0.1);}
        &.button-disabled { --background: transparent !important; background-color: #E8E8E8 !important; opacity: .4;}
    }
    .btn_prev { background-image: url("../../../assets/images/btn_ico_prev.svg");}
    .btn_next { background-image: url("../../../assets/images/btn_ico_next.svg");}
    
}
.right { justify-content: flex-end;}
ion-label { font-size: 16px; color: #000000;}
